import * as React from 'react';
import { connect } from 'react-redux';
import { isPassed, isFailed, isInProgress } from 'utils/comparison';
import { withTheme } from 'styled-components';
import { Tooltip } from 'components/common';
import { localize } from 'core/localization';
import { transparentize } from 'utils/color';
import CircularProgressbar from 'react-circular-progressbar';
import { getColors, shouldSubmitAllQuestions } from 'store/settings/selectors';
import {
  getAffectProgressQuestionsCountInSection,
  getCorrectlyAffectProgressQuestionsCountInSection,
  getAffectCorrectlyAnsweredQuestionsCount,
  getAnsweredSectionQuestionsCount,
  getSectionProgress
} from 'store/sections/selectors';

import { isAttemptSubmitted } from 'store/course/selectors';
import { RootAppState } from 'store/types';
import { KEYS } from 'constants/common';
import {
  PopoverContentWrapper,
  PopoverContent,
  ClosePopoverIcon,
  QuestionsProgress,
  CongratsMessageWrapper,
  CongratsMessageTitle,
  NotPassedMessageWrapper,
  FailedMessageWrapper,
  FailedMessageTitle,
  PopoverButton,
  QuestionProgressText
} from './ProgressPopover.styled';

type ProgressPopoverProps = {
  score: number;
  status: number;
  affectProgressQuestionsCountInSection?: number;
  colors?: { [key: string]: any };
  popoverPosition: any;
  children?: any;
  correctlyAffectProgressQuestionsCountInSection?: number;
  affectCorrectlyAnsweredQuestionsCount?: number;
  answeredSectionQuestionsCount?: number;
  sectionProgress?: number;
  isSubmitAtOnce?: boolean;
  isCourseSubmitted?: boolean;
  sectionId: string;
  questions?: any;
  theme?: any;
  ariaLabelledby?: string;
};

type ProgressPopoverState = {
  isExpanded: boolean;
};
export class ProgressPopover extends React.Component<ProgressPopoverProps, ProgressPopoverState> {
  popoverRef: any;
  constructor(props: ProgressPopoverProps) {
    super(props);
    this.state = {
      isExpanded: false
    };
    this.popoverRef = React.createRef()
  }

  toggleOnClick = (evt: any) => {
    if (!evt.key) {
      this.setState({
        isExpanded: !this.state.isExpanded
      });
    }
  };

  closePopup = () => {
    if (this.state.isExpanded) {
      this.setState({
        isExpanded: false
      });
    }
  };

  handleClosePopupKeyDown = (evt: any) => {
    if ([KEYS.ENTER.toLowerCase(), KEYS.ESCAPE.toLowerCase()].includes(evt.key.toLowerCase())) {
      this.closePopup();
    }
  };

  componentDidUpdate() {
    setTimeout(() => {
      const element = document.getElementById('progress-popover');
      if (element && this.state.isExpanded) {
        element.focus();
      }
      if (this.popoverRef.current && !this.state.isExpanded) {
        this.popoverRef.current.focus();
      }
    }, 0);
  }

  shouldComponentUpdate(nextProps: ProgressPopoverProps, nextState: ProgressPopoverState) {
    return this.state.isExpanded !== nextState.isExpanded;
  }

  render() {
    const { isExpanded } = this.state;
    const { popoverPosition, ariaLabelledby } = this.props;

    return (
      <Tooltip
        style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }}
        trigger={'click'}
        open={isExpanded}
        onRequestClose={this.closePopup}
        position={popoverPosition}
        tooltipTheme={'light'}
        arrow={true}
        interactive
        html={this.renderPopoverContent()}
      >
        <PopoverButton
          tabIndex={0}
          onClick={this.toggleOnClick}
          ref={this.popoverRef}
          aria-labelledby={ariaLabelledby}
        />
      </Tooltip>
    );
  }

  getCircularProgressbarStroke = () => {
    const { status, colors } = this.props;
    return isFailed(status) && colors ? colors.incorrectColor : colors && colors.mainColor;
  };

  renderSubmitOnceQuestionProgress = () => {
    const {
      status,
      correctlyAffectProgressQuestionsCountInSection,
      affectProgressQuestionsCountInSection,
      answeredSectionQuestionsCount,
      sectionProgress = 0,
      isCourseSubmitted = false,
      score,
      colors
    } = this.props;
    const templateTheme = this.props.theme;
    const isSectionPassed = isCourseSubmitted && isPassed(status);
    return (
      <React.Fragment>
        <QuestionsProgress isPassed={isSectionPassed} theme={templateTheme}>
          <QuestionProgressText>
            {isCourseSubmitted
              ? correctlyAffectProgressQuestionsCountInSection
              : answeredSectionQuestionsCount}
            /{affectProgressQuestionsCountInSection}
          </QuestionProgressText>
          <CircularProgressbar
            strokeWidth={2}
            percentage={isCourseSubmitted ? score : sectionProgress}
            styles={{
              path: {
                stroke: this.getCircularProgressbarStroke(),
                strokeLinecap: 'butt',
                transition: 'stroke-dashoffset 0.5s ease'
              },
              trail: {
                stroke: colors && transparentize(colors.textColor, 0.02)
              }
            }}
          />
        </QuestionsProgress>
        {this.renderPopoverMessageContent()}
      </React.Fragment>
    );
  };

  renderQuestionProgress = () => {
    const {
      status,
      correctlyAffectProgressQuestionsCountInSection,
      affectProgressQuestionsCountInSection,
      score,
      colors
    } = this.props;
    const templateTheme = this.props.theme;
    const isSectionPassed = isPassed(status);
    return (
      <React.Fragment>
        <QuestionsProgress isPassed={isSectionPassed} theme={templateTheme}>
          <QuestionProgressText>
            {correctlyAffectProgressQuestionsCountInSection}/{affectProgressQuestionsCountInSection}
          </QuestionProgressText>
          <CircularProgressbar
            strokeWidth={2}
            percentage={score}
            styles={{
              path: {
                stroke: this.getCircularProgressbarStroke(),
                strokeLinecap: 'butt',
                transition: 'stroke-dashoffset 0.5s ease'
              },
              trail: {
                stroke: colors && transparentize(colors.textColor, 0.02)
              }
            }}
          />
        </QuestionsProgress>
        {this.renderPopoverMessageContent()}
      </React.Fragment>
    );
  };

  renderPopoverContent = () => {
    const { isSubmitAtOnce } = this.props;
    const templateTheme = this.props.theme;

    return (
      <PopoverContentWrapper
        id="progress-popover"
        role="dialog"
        ariaModal={true}
        tabIndex={-1}
        onKeyDown={event => this.handleClosePopupKeyDown(event)}
        theme={templateTheme}
      >
        <PopoverContent>
          <ClosePopoverIcon
            role="button"
            ariaLabel={localize('[close popover]')}
            tabIndex={0}
            onKeyDown={(event: any) => this.handleClosePopupKeyDown(event)}
            onClick={this.toggleOnClick}
            size={14}
            name="close-popup"
            theme={templateTheme}
          />
          {isSubmitAtOnce ? this.renderSubmitOnceQuestionProgress() : this.renderQuestionProgress()}
        </PopoverContent>
      </PopoverContentWrapper>
    );
  };

  renderPopoverMessageContent = () => {
    const {
      status,
      affectProgressQuestionsCountInSection,
      theme,
      affectCorrectlyAnsweredQuestionsCount
    } = this.props;
    return (
      <>
        {isPassed(status) && (
          <CongratsMessageWrapper>
            <CongratsMessageTitle theme={theme} appearance="h4">
              {localize('[passed section title]')}
            </CongratsMessageTitle>
            {localize('[passed section]')}
          </CongratsMessageWrapper>
        )}
        {isFailed(status) && (
          <FailedMessageWrapper>
            <FailedMessageTitle theme={theme} appearance="h4">
              {localize('[failed section title]')}
            </FailedMessageTitle>
            {localize('[failed section x y]', {
              countCorrectlyAnsweredQuestionsInSection: affectCorrectlyAnsweredQuestionsCount,
              countQuestionsInSection: affectProgressQuestionsCountInSection
            })}
          </FailedMessageWrapper>
        )}
        {isInProgress(status) && (
          <NotPassedMessageWrapper>
            {localize('[you need to answer X questions out of Y]', {
              correctQuestionsCount: affectCorrectlyAnsweredQuestionsCount,
              totalQuestionsCount: affectProgressQuestionsCountInSection
            })}
          </NotPassedMessageWrapper>
        )}
      </>
    );
  };
}

function mapStateToProps(state: RootAppState, ownProps: ProgressPopoverProps) {
  return {
    colors: getColors(state),
    affectProgressQuestionsCountInSection: getAffectProgressQuestionsCountInSection(
      state,
      ownProps.sectionId
    ),
    correctlyAffectProgressQuestionsCountInSection: getCorrectlyAffectProgressQuestionsCountInSection(
      state,
      ownProps.sectionId
    ),
    affectCorrectlyAnsweredQuestionsCount: getAffectCorrectlyAnsweredQuestionsCount(
      state,
      ownProps.sectionId
    ),
    sectionProgress: getSectionProgress(state, ownProps.sectionId),
    answeredSectionQuestionsCount: getAnsweredSectionQuestionsCount(state, ownProps.sectionId),
    isSubmitAtOnce: shouldSubmitAllQuestions(state),
    isCourseSubmitted: isAttemptSubmitted(state)
  };
}

export default withTheme(connect(mapStateToProps)(ProgressPopover));
