import * as React from 'react';
import { Link } from 'react-router-dom';
import { localize } from 'core/localization';
import { MobileFooterContainer, LinkToPageContainer, LinkToPageText } from './MobileFooter.styled';

type MobileFooterProps = {
  navigationTextKey: string;
  navigateTo: string;
  navigationToTextKey: string;
};

export class MobileFooter extends React.Component<MobileFooterProps, {}> {
  render() {
    const { navigationTextKey, navigateTo, navigationToTextKey } = this.props;
    return (
      <MobileFooterContainer>
        <LinkToPageContainer>
          <LinkToPageText>
            {localize(navigationTextKey)}
            <Link to={navigateTo}>{localize(navigationToTextKey)}</Link>
          </LinkToPageText>
        </LinkToPageContainer>
      </MobileFooterContainer>
    );
  }
}

export default MobileFooter;
