import * as React from 'react';
import { localize } from 'core/localization';
import { Collapse } from 'components/common';

import { KEYS } from 'constants/common';
import {
  CollapseWrapper,
  CollapseInnerWrapper,
  CollapseButton,
  ButtonText,
  IconCircleObjective,
  IconClose,
  LearningObjectiveText,
  CollapseContent
} from './SectionLearningObjective.styled';

type SectionLearningObjectiveProps = {
  learningObjective: string;
};

type SectionLearningObjectiveState = {
  isExpanded: boolean;
};

class SectionLearningObjective extends React.Component<
  SectionLearningObjectiveProps,
  SectionLearningObjectiveState
> {
  constructor(props: SectionLearningObjectiveProps) {
    super(props);
    this.state = { isExpanded: false };
  }

  toggleExpanded = () => {
    this.setState((prevState: SectionLearningObjectiveState) => ({
      isExpanded: !prevState.isExpanded
    }));
  };

  onKeyDown = (event: any) => {
    if (
      event.key?.toLowerCase() === KEYS.SPACEBAR.toLowerCase() ||
      event.key?.toLowerCase() === KEYS.ENTER.toLowerCase()
    ) {
      event.preventDefault();
      this.toggleExpanded();
    }
  };

  render() {
    const { isExpanded } = this.state;
    const { learningObjective } = this.props;
    return (
      <React.Fragment>
        <CollapseButton
          tabIndex={0}
          onKeyDown={(event: any) => this.onKeyDown(event)}
          onClick={() => this.toggleExpanded()}
        >
          <IconCircleObjective name="circle-objective" size={11} color="textColor" />
          <ButtonText>
            {localize(isExpanded ? '[hide learning objective]' : '[show learning objective]')}
          </ButtonText>
        </CollapseButton>

        <CollapseWrapper>
          <Collapse isExpanded={isExpanded}>
            <CollapseInnerWrapper>
              <IconClose
                role="button"
                tabIndex={0}
                onKeyDown={(event: any) => this.onKeyDown(event)}
                onClick={() => this.toggleExpanded()}
                name="close"
                size={7}
                color="textColor"
              />
              <LearningObjectiveText appearance={'p'}>
                {localize('[learning objective]')}
              </LearningObjectiveText>
              <CollapseContent appearance={'p'}>{learningObjective}</CollapseContent>
            </CollapseInnerWrapper>
          </Collapse>
        </CollapseWrapper>
      </React.Fragment>
    );
  }
}

export default SectionLearningObjective;
