import * as React from 'react';
import { connect } from 'react-redux';
import { localize } from 'core/localization';
import { isPassed, isFailed } from 'utils/comparison';
import { Icon } from 'components/common';
import { maxCountTocNavigationElements } from 'constants/common';
import CircularProgressbar from 'react-circular-progressbar';
import ProgressPopover from 'components/common/ProgressPopover';
import { getColors, shouldSubmitAllQuestions } from 'store/settings/selectors';
import { transparentize } from 'utils/color';
import Header from '../Header/Header';
import NavigationBlock from '../NavigationBlock/NavigationBlock';
import NavigationItem, { NavigationLearningItem } from '../NavigationItem/NavigationItem';
import ResultsBlock from '../ResultsBlock/ResultsBlock';
import CollapsedTocPoolSection from '../CollapsedTocPoolSection';
import { CollapsedTocContainer, SectionProgress, StatusIcon } from './CollapsedToc.styled';
import { RootAppState } from 'store/types';

const constants = {
  minimalShownProgress: 10
};

type CollapsedTocProps = {
  isExpanded: boolean;
  courseStructure: {
    title: string;
    sections: any[];
    isAttemptSubmitted: boolean;
  };
  activeSectionId: string;
  activeQuestionId: string;
  navigateToIndex(): void;
  navigateToQuestion(sectionId: string, questionId: string, isCourseOnOnePage: boolean): void;
  navigateToResults(): void;
  navigateToLearningObjective(id: any, isCourseOnOnePage: boolean): void;
  isResultsActive: boolean;
  isLearningObjectiveView: boolean;
  colors: { [key: string]: any };
  questionsForPool: any;
  isQuestionPool: boolean;
  isCourseOnOnePage: boolean;
  isSubmitAtOnce: boolean;
};
export class CollapsedToc extends React.Component<CollapsedTocProps, {}> {
  showProgressScore = (score: number) => {
    if (score <= constants.minimalShownProgress) {
      return constants.minimalShownProgress;
    }
    return score;
  };

  renderSubmitOnceSectionProgress = (section: any, isAttemptSubmitted: boolean) => {
    if (isAttemptSubmitted) {
      return this.showProgressScore(section.score);
    }
    return this.showProgressScore(section.sectionProgress);
  };

  renderSectionProgress = (section: any) => {
    return this.showProgressScore(section.score);
  };

  renderCircularProgressbar(section: any) {
    const { colors, courseStructure, isSubmitAtOnce } = this.props;
    const isAttemptSubmitted = courseStructure.isAttemptSubmitted;
    return (
      <CircularProgressbar
        strokeWidth={24}
        percentage={
          isSubmitAtOnce
            ? this.renderSubmitOnceSectionProgress(section, isAttemptSubmitted)
            : this.renderSectionProgress(section)
        }
        styles={{
          path: {
            stroke: isFailed(section.status) ? colors.incorrectColor : colors.mainColor,
            strokeLinecap: 'butt',
            transition: 'stroke-dashoffset 0.5s ease 0s'
          },
          trail: {
            stroke: transparentize(colors.textColor, 0.02),
            transition: 'stroke 0.2s ease 0s'
          }
        }}
      />
    );
  }

  isPassedSection = (section: any) => {
    const { courseStructure, isSubmitAtOnce } = this.props;
    const isAttemptSubmitted = courseStructure.isAttemptSubmitted;

    if (isSubmitAtOnce) {
      return isAttemptSubmitted && isPassed(section.status);
    } else {
      return isPassed(section.status);
    }
  };

  getSectionData = () => {
    const { activeSectionId, courseStructure, activeQuestionId} = this.props;
    let section: any = null;
    let questions: any[] = [];
    let activeQuestionIndex = -1;
    let activeSectionIndex = -1;

    if (activeSectionId) {
      section = courseStructure.sections.find((item, index) => {
        if (item.id === activeSectionId) {
          activeSectionIndex = index;
          return true;
        }
        return false;
      }) || {};
      questions = (section.questions || []).map((question: any, questionIndex: any) => ({
        key: `${questionIndex}`,
        question,
        isAnsweredCorrectly: question.isAnsweredCorrectly,
        affectsProgress: question.affectsProgress,
        sectionId: section.id
      }));
      activeQuestionIndex = questions.findIndex(item => item.question.id === activeQuestionId);
    }
    return { section, questions, activeQuestionIndex, activeSectionIndex}
  }

  render() {
    const {
      courseStructure,
      isExpanded,
      activeQuestionId,
      activeSectionId,
      navigateToIndex,
      navigateToQuestion,
      navigateToResults,
      navigateToLearningObjective,
      isResultsActive,
      isLearningObjectiveView,
      questionsForPool,
      isQuestionPool,
      isCourseOnOnePage,
      isSubmitAtOnce
    } = this.props;

    const isAttemptSubmitted = courseStructure.isAttemptSubmitted;
    const { section, questions, activeQuestionIndex, activeSectionIndex } = this.getSectionData();

    return (
      <CollapsedTocContainer isExpanded={isExpanded} ariaLabel={localize('[navigation section]')}>
        <Header collapsed navigateToIndex={navigateToIndex} tabIndex={isExpanded ? -1 : 0} />
        {isQuestionPool && (
          <CollapsedTocPoolSection
            questionsForPool={questionsForPool}
            navigateToQuestion={navigateToQuestion}
            activeQuestionId={activeQuestionId}
            activeQuestionIndex={activeQuestionIndex}
            isSubmitAtOnce={isSubmitAtOnce}
            isAttemptSubmitted={isAttemptSubmitted}
          />
        )}
        {!isQuestionPool && activeSectionId && (
          <NavigationBlock
            activeItemIndex={activeQuestionIndex}
            navigationElementsCount={(questions.length as any) + !!section.learningObjective}
            role="menu"
          >
            {section.learningObjective && (
              <NavigationLearningItem
                onClick={() => navigateToLearningObjective(section.id, isCourseOnOnePage)}
                onKeyDown={event => {
                  if (event.key === 'Enter') {
                    navigateToLearningObjective(section.id, isCourseOnOnePage);
                  }
                }}
                role="menuitem"
                tabIndex={0}
                isActive={isLearningObjectiveView}
              />
            )}
            {questions.map(item => (
              <NavigationItem
                key={item.key}
                role="menuitem"
                tabIndex={0}
                question={item.question}
                isActive={item.question.id === activeQuestionId}
                shouldSubmitAllQuestions={isSubmitAtOnce}
                isAttemptSubmitted={isAttemptSubmitted}
                {...(item.question.id !== activeQuestionId && {
                  onClick: () =>
                    navigateToQuestion(item.sectionId, item.question.id, isCourseOnOnePage),
                  onKeyDown: event => {
                    if (event.key === 'Enter') {
                      navigateToQuestion(item.sectionId, item.question.id, isCourseOnOnePage);
                    }
                  }
                })}
              />
            ))}
          </NavigationBlock>
        )}
        {!isQuestionPool && section && section.affectsProgress && (
          <SectionProgress isNavigationExist={maxCountTocNavigationElements < questions.length}>
            <ProgressPopover
              popoverPosition={'right-end'}
              questions={questions}
              score={section.score}
              status={section.status}
              sectionId={section.id}
              ariaLabelledby={`section-status-${activeSectionIndex}`}
            />
            {this.isPassedSection(section) ? (
              <StatusIcon>
                <Icon name="ok" size={6} color="buttonTextColor" />
              </StatusIcon>
            ) : (
              this.renderCircularProgressbar(section)
            )}
          </SectionProgress>
        )}

        { !isSubmitAtOnce && <ResultsBlock
          ariaLabel={localize('[results]')}
          collapsed
          isActive={isResultsActive}
          navigateToResults={navigateToResults}
          tabIndex={isExpanded ? -1 : 0}
        />}
      </CollapsedTocContainer>
    );
  }
}

function mapStateToProps(state: RootAppState) {
  return {
    colors: getColors(state),
    isSubmitAtOnce: shouldSubmitAllQuestions(state)
  };
}

export default connect(mapStateToProps)(CollapsedToc);
