import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import * as courseTimerActions from 'store/timer/actions';
import { localize } from 'core/localization';
import * as questionActions from 'store/questions/actions';
import { BUTTON, TOOLTIP } from 'constants/components';
import { INFORMATION_CONTENT, SCENARIO } from 'constants/questionTypes';
import { Button, CssAnimation, StatusTooltip } from 'components/common';
import {
  isQuestionAnswered,
  isQuestionAnsweredCorrectly,
  isFeedbackAnimating,
  getAffectProgressQuestionsCount
} from 'store/questions/selectors';
import {
  isQuestionPoolEnabled,
  isAttemptsLimited,
  getLimitedAttemptsNumber,
  getTimerEnabled
} from 'store/settings/selectors';
import { getTimerStatus } from 'store/timer/selectors';
import { TimerStatus } from 'constants/timer';
import { RootAppState } from 'store/types.js';
import {
  RemainingAttemptsReminder,
  RemainingAttemptsReminderText,
  ActionButtonsContainer,
  NavButtonsBlock,
  SurveyHint
} from './ActionsBlock.styled';

type ActionBlocksState = {
  isJustSubmitted: boolean;
  showTooltip: boolean;
  isInformationContent: any;
  canGoNext: boolean;
};

export type ActionBlocksProps = {
  actions?: { [key: string]: any };
  question: { [key: string]: any };
  goToNextPage(): void;
  isNextPageResults: boolean;
  feedbackAnimated?: boolean;
  questionCountInSection: number;
  questionIndex: number;
  isQuestionPool?: boolean;
  scrollToId?(id?: string): void;
  currentScrollLocationId?: any;
  isLastBlock?: any;
  isCourseOnOnePage?: boolean;
  isLimitedAttempts?: boolean;
  limitedAttemptsNumber?: number;
  timerStatus?: TimerStatus;
  timerActions?: { [key: string]: any };
  isTimerEnabled?: boolean;
  affectProgressQuestionsCount?: number;
};
export class ActionBlocks<T extends ActionBlocksProps> extends React.Component<
  T,
  ActionBlocksState
> {
  constructor(props: T) {
    super(props);
    this.state = {
      isJustSubmitted: false,
      showTooltip: false,
      isInformationContent: this.props.question.type === INFORMATION_CONTENT,
      canGoNext: false
    };
  }

  componentDidMount() {
    this.setState({
      canGoNext: this.canGoToNextQuestion()
    });
  }

  componentDidUpdate(prevProps: ActionBlocksProps) {
    if (prevProps.question.id !== this.props.question.id) {
      this.setState({ isJustSubmitted: false });
    }
  }

  render() {
    const { isJustSubmitted, isInformationContent } = this.state;
    const { question, feedbackAnimated = false } = this.props;
    const { isSurvey } = question;
    const canGoNext = this.canGoToNextQuestion();
    const remainingAttempts = this.getRemainingAttempts();
    const isAttemptAllowed = this.isAttemptAllowed();
    return (
      <ActionButtonsContainer isHidden={feedbackAnimated}>
        {!isInformationContent && (!question.isAnswered || feedbackAnimated) && (
          <React.Fragment>
            {question.type === SCENARIO ? (
              <StatusTooltip
                popoverPosition={'top'}
                layout={TOOLTIP.INCORRECT}
                onRequestClose={this.hidePopover}
                showTooltip={this.state.showTooltip}
                message={localize('[scenario tooltip text]')}
                popoverButton={
                  <Button tabIndex={0} onClick={this.submit} onBlur={this.hidePopover}>
                    {localize('[submit]')}
                  </Button>
                }
              />
            ) : (
              <React.Fragment>
                <Button tabIndex={0} onClick={this.submit}>
                  {localize('[submit]')}
                </Button>
                {isSurvey && !canGoNext && (
                  <SurveyHint>{localize('[survey question hint]')}</SurveyHint>
                )}
              </React.Fragment>
            )}
            {isAttemptAllowed && (
              <RemainingAttemptsReminder>
                <RemainingAttemptsReminderText
                  data-test="remaining-attempts"
                  ellipsis
                  ariaLabel={`${localize('[remaining attempts]')} ${remainingAttempts}`}
                  tabIndex={-1}
                  role="status"
                >
                  {`(${localize('[remaining attempts]')} ${remainingAttempts})`}
                </RemainingAttemptsReminderText>
              </RemainingAttemptsReminder>
            )}
          </React.Fragment>
        )}
        {!feedbackAnimated &&
          (isJustSubmitted ? (
            <CssAnimation type={CssAnimation.AnimationType.slideInDown}>
              {this.renderNavButtonsBlock()}
            </CssAnimation>
          ) : (
            this.renderNavButtonsBlock()
          ))}
      </ActionButtonsContainer>
    );
  }

  nextButtonLayout() {
    const { question } = this.props;
    const { isAnsweredCorrectly, isSurvey } = question;
    const isInformationContent = question.type === INFORMATION_CONTENT;
    return isInformationContent || isAnsweredCorrectly || isSurvey
      ? BUTTON.LAYOUT.PRIMARY
      : BUTTON.LAYOUT.SECONDARY;
  }

  renderNavButtonsBlock() {
    const {
      question,
      questionIndex,
      questionCountInSection,
      isCourseOnOnePage,
      isNextPageResults
    } = this.props;
    const { isAnsweredCorrectly, isSurvey } = question;
    const isLastPageInSection = questionIndex + 1 === questionCountInSection;
    const isInformationContent = question.type === INFORMATION_CONTENT;
    const canGoNext = this.canGoToNextQuestion();

    return (
      <NavButtonsBlock tabIndex={-1}>
        {this.canTryAgainQuestion() && (
          <Button tabIndex={0} onClick={this.tryAgain}>
            {localize('[try again]')}
          </Button>
        )}
        {!isCourseOnOnePage &&
          canGoNext &&
          this.renderNextButton(
            isInformationContent,
            isAnsweredCorrectly,
            isSurvey,
            isLastPageInSection
          )}

        {isCourseOnOnePage &&
          isNextPageResults &&
          this.renderNextButton(
            isInformationContent,
            isAnsweredCorrectly,
            isSurvey,
            isLastPageInSection
          )}
      </NavButtonsBlock>
    );
  }

  renderNextButton(
    isInformationContent: boolean,
    isAnsweredCorrectly: boolean,
    isSurvey: boolean,
    isLastPageInSection: boolean
  ) {
    const {
      timerActions,
      isTimerEnabled,
      isNextPageResults,
      affectProgressQuestionsCount
    } = this.props;
    const goToNextPage = () => {
      if (
        timerActions &&
        isTimerEnabled &&
        isNextPageResults &&
        affectProgressQuestionsCount <= 0
      ) {
        timerActions.setTimerStatus(TimerStatus.STOPPED);
        timerActions.updateTimerProgress();
      }
      const { goToNextPage } = this.props;
      goToNextPage();
    };

    return (
      <Button
        data-test="button-next"
        layout={this.nextButtonLayout()}
        onClick={goToNextPage}
        tabIndex={0}
        suffixIcon="arrow-down"
      >
        {this.getNextNavigationText(isLastPageInSection)}
      </Button>
    );
  }

  hidePopover = async () => {
    this.setState({ showTooltip: false });
  };

  submit = () => {
    const { question } = this.props;
    if (question.type === SCENARIO) {
      if (question.response.isFinished) {
        this.props.actions && this.props.actions.answer(question.id);
        this.props.actions &&
          this.props.actions.animateFeedback({
            questionId: question.id,
            isAnimating: true
          });
        this.setState({ isJustSubmitted: true, showTooltip: false });
      } else {
        this.setState({ showTooltip: true });
      }
    } else {
      this.props.actions && this.props.actions.answer(question.id);
      this.props.actions &&
        this.props.actions.animateFeedback({
          questionId: question.id,
          isAnimating: true
        });
      this.setState({
        isJustSubmitted: true,
        canGoNext: this.canGoToNextQuestion()
      });
    }
  };

  getNextNavigationText = (isLastPageInSection: boolean) => {
    const { isNextPageResults, isQuestionPool } = this.props;
    if (isNextPageResults) {
      return localize('[go to results]');
    }
    if (isQuestionPool) {
      return localize('[next]');
    }
    return localize(isLastPageInSection ? '[next section]' : '[next]');
  };

  canGoToNextQuestion() {
    return this.props.question.type === INFORMATION_CONTENT ? true : this.props.question.isAnswered;
  }

  canTryAgainQuestion() {
    const { question, isLimitedAttempts, timerStatus } = this.props;
    const isAttemptAllowed = this.isAttemptAllowed() || !isLimitedAttempts;
    const isTimerStopped = timerStatus === TimerStatus.STOPPED;

    return (
      isAttemptAllowed &&
      question.type !== INFORMATION_CONTENT &&
      question.isAnswered &&
      !question.isAnsweredCorrectly &&
      !question.isSurvey &&
      !isTimerStopped
    );
  }

  isAttemptAllowed() {
    const { question, isLimitedAttempts } = this.props;
    const remainingAttempts = this.getRemainingAttempts();
    return (
      isLimitedAttempts &&
      !question.isSurvey &&
      !question.isAnsweredCorrectly &&
      remainingAttempts > 0
    );
  }

  getRemainingAttempts() {
    const { question, isLimitedAttempts, limitedAttemptsNumber } = this.props;
    return isLimitedAttempts && limitedAttemptsNumber
      ? (limitedAttemptsNumber as number) - question.attemptNumber
      : 0;
  }

  tryAgain = () => {
    const { question } = this.props;
    this.props.actions && this.props.actions.resetProgress(question);
    this.props.actions && this.props.actions.updateRetries(question.id);
    this.setState({ isJustSubmitted: false });
    this.props.actions &&
      this.props.actions.animateFeedback({ questionId: question.id, isAnimating: true });
  };
}

function mapStateToProps(state: RootAppState, ownProps: ActionBlocksProps) {
  const questionId = ownProps.question.id;
  return {
    isAnswered: isQuestionAnswered(state, questionId),
    isAnsweredCorrectly: isQuestionAnsweredCorrectly(state, questionId),
    feedbackAnimated: isFeedbackAnimating(state, questionId),
    isLimitedAttempts: isAttemptsLimited(state),
    limitedAttemptsNumber: getLimitedAttemptsNumber(state),
    timerStatus: getTimerStatus(state),
    isQuestionPool: isQuestionPoolEnabled(state),
    isTimerEnabled: getTimerEnabled(state),
    affectProgressQuestionsCount: getAffectProgressQuestionsCount(state)
  };
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    actions: bindActionCreators(questionActions, dispatch),
    timerActions: bindActionCreators(courseTimerActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ActionBlocks);
