import React, { Component } from 'react';
import { connect } from 'react-redux';
import { RootAppState } from '../../../store/types';
import {
  isAllQuestionsOnOnePage,
  shouldSubmitAllQuestions
} from '../../../store/settings/selectors';
import DefaultLayout from './defaultLayout/DefaultLayout';
import OnePageCourse from './singlePageLayout/OnePageCourse';

type CourseLayoutProps = {
  isCourseOnOnePage: boolean;
  isSubmitAtOnce: boolean;
};

export class CourseLayout extends Component<CourseLayoutProps, {}> {
  render() {
    const { isCourseOnOnePage } = this.props;
    if (isCourseOnOnePage) {
      return <OnePageCourse {...this.props} />;
    }
    return <DefaultLayout {...this.props} />;
  }
}

function mapDispatchToProps() {
  return {};
}

function mapStateToProps(state: RootAppState) {
  return {
    isCourseOnOnePage: isAllQuestionsOnOnePage(state),
    isSubmitAtOnce: shouldSubmitAllQuestions(state)
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(CourseLayout);
