import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import shuffleAnswerOptions from 'utils/questions';
import { getUserAnswers, getUserResponse } from 'store/questions/rankingText/selectors';
import * as actions from 'store/questions/rankingText/actions';
import { DragDrop } from 'components/common';
import { RootAppState } from 'store/types';
import * as questionActions from '../../../store/questions/actions';
import { isRandomizeAnswersEnabled } from 'store/settings/selectors';

type RankingTextProps = {
  id: string;
  type: string;
  answers?: Array<any>;
  actions?: { [key: string]: any };
  isMultipleList?: boolean;
  questionActions?: { [key: string]: any };
  allowSubmit: boolean;
  response?: Array<any>;
  randomizeAnswersEnabled?: boolean;
  isAnswered: boolean;
};

export class RankingText extends React.Component<RankingTextProps, {}> {
  moveItem(
    items: any,
    id: string,
    action: any,
    allowSubmit: boolean,
    questionAction: any,
    initialLoading = false
  ) {
    action.rankingAnswersAreSelected(items, id);
    if (allowSubmit && !initialLoading) {
      questionAction && questionAction.answerBeforeSubmit(id);
    }
  }

  getElements() {
    const { answers = [], response = [], randomizeAnswersEnabled } = this.props;
    if (!response.length) {
      if (randomizeAnswersEnabled) {
        return answers;
      }
      return shuffleAnswerOptions(answers);
    }
    return response;
  }

  render() {
    const listsOfItems = [{ id: this.props.id, items: this.getElements() }];
    return (
      <DragDrop
        allowSubmit={this.props.allowSubmit}
        moveItem={this.moveItem}
        questionActions={this.props.questionActions}
        actions={this.props.actions}
        id={this.props.id}
        isMultipleList={false}
        listsOfItems={listsOfItems}
        isAnswered={this.props.isAnswered}
      />
    );
  }
}

function mapStateToProps(state: RootAppState, ownProps: RankingTextProps) {
  return {
    answers: getUserAnswers(state, ownProps.id),
    response: getUserResponse(state, ownProps.id),
    randomizeAnswersEnabled: isRandomizeAnswersEnabled(state)
  };
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch),
    questionActions: bindActionCreators(questionActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(RankingText);
