import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { INFORMATION_CONTENT } from 'constants/questionTypes';
import { REVIEW_COMMENT_PROPERTIES, REVIEW_COMMENT_TYPES } from 'constants/review';
import {
  getSection,
  getQuestionIndex,
  getQuestionCount,
  sectionExists
} from 'store/sections/selectors';
import {
  getBackground,
  getLogo,
  isMainLogoShown,
  isShowLearningHintsEnabled,
  isQuestionPoolEnabled,
  getQuestionPoolSize,
  isAllQuestionsOnOnePage
} from 'store/settings/selectors';
import { Separator, CourseLogo } from 'components/common';
import Contents from 'components/contents/Contents';
import withFallbackToNotFound from 'components/hocs/withFallbackToNotFound';
import withNavigation from 'components/hocs/withNavigation';
import Question from 'components/questions/Question';
import Reviewable from 'components/review/Reviewable';
import { localize } from 'core/localization';
import * as questionActions from 'store/questions/actions';
import {
  getQuestion,
  isQuestionAnswered,
  isQuestionAnsweredCorrectly,
  questionExists,
  getQuestionResponse,
  getPoolQuestionIndex
} from 'store/questions/selectors';
import PageCounter from 'components/question/PageCounter';
import PoolCounter from 'components/question/PoolCounter';
import Feedback from 'components/question/Feedback';
import Hint from 'components/question/Hint';
import VoiceOver from 'components/question/VoiceOver';
import { getCourseTitle } from 'store/course/selectors';
import { RootAppState } from 'store/types';
import ActionBlock from './actionsBlock/ActionsBlock';
import { QuestionTitle, QuestionTitleWrapper } from './QuestionPage.styled';
import { SKIP_TO_CONTENT_IDS } from 'constants/common';

export type QuestionPageProps = {
  section: { [key: string]: any };
  question: { [key: string]: any };
  actions: { [key: string]: any };
  navigateToNextPage(): void;
  checkIsNextPageResults(): boolean;
  questionIndex: number;
  questionCountInSection: number;
  bodyBackground: { [key: string]: any };
  logoUrl: string;
  logoShown: boolean;
  title: string;
  questionResponse: any[];
  isQuestionPool: boolean;
  questionPoolSize: number;
  poolQuestionIndex: number;
  sectionId: any;
  questionId: any;
  currentScrollLocationId: any;
  scrollToId(id: string): void;
  isLastBlock: any;
  isCourseOnOnePage: boolean;
  onePageQuestionIndex: number;
  questionCountInOnePage: number;
  showLearningHints: boolean;
  shouldRecalculateScrollLocation(reCalculate: boolean): any;
};

export type QuestionPageState = {
  showTooltip: boolean;
  feedbackContentList: any[];
};
export class QuestionPage<T extends QuestionPageProps> extends Component<T, QuestionPageState> {
  questionPageRef: any;
  constructor(props: T) {
    super(props);
    this.state = {
      showTooltip: false,
      feedbackContentList: []
    };
    this.questionPageRef = React.createRef();
  }

  componentDidMount() {
    if (!this.props.isCourseOnOnePage) {
      this.props.actions.markAsOpened(this.props.question.id);
      this.setTitlePage();
    }
    this.props.actions.loadFeedback(this.props.question.id);
  }

  componentDidUpdate(prevProps: QuestionPageProps) {
    if (prevProps.question.id !== this.props.question.id) {
      this.props.actions.loadFeedback(this.props.question.id);
    }
    if (this.props.isCourseOnOnePage) {
      this.props.shouldRecalculateScrollLocation(true);
    }
  }

  hidePopover = async () => {
    this.setState({ showTooltip: false });
  };

  getNextNavigationText = (isLastPageInSection: boolean) => {
    const { checkIsNextPageResults } = this.props;
    if (checkIsNextPageResults()) {
      return localize('[go to results]');
    }
    return localize(isLastPageInSection ? '[next section]' : '[next]');
  };

  setTitlePage = () => {
    const { question } = this.props;
    const isInformationContent = question.type === INFORMATION_CONTENT;
    if (isInformationContent) {
      document.title = `${localize('[content page title]')} | ${this.props.title}`;
    } else {
      document.title = `${localize('[questions page title]')} | ${this.props.title}`;
    }
  };

  render() {
    const {
      question,
      questionIndex,
      questionCountInSection,
      bodyBackground,
      logoUrl,
      logoShown,
      isQuestionPool,
      questionPoolSize,
      poolQuestionIndex,
      currentScrollLocationId,
      isLastBlock,
      isCourseOnOnePage,
      onePageQuestionIndex,
      questionCountInOnePage
    } = this.props;
    const questionType = question.type === INFORMATION_CONTENT ? INFORMATION_CONTENT : 'question';

    return (
      <Fragment>
        {logoShown && !isCourseOnOnePage && <CourseLogo logoUrl={logoUrl} />}
        {isQuestionPool && <PoolCounter index={poolQuestionIndex + 1} total={questionPoolSize} />}
        {!isQuestionPool && (
          <PageCounter
            index={isCourseOnOnePage ? onePageQuestionIndex + 1 : questionIndex + 1}
            total={isCourseOnOnePage ? questionCountInOnePage : questionCountInSection}
            isCourseOnOnePage={isCourseOnOnePage}
            questionType={questionType}
          />
        )}
        <QuestionTitleWrapper
          ref={this.questionPageRef}
          tabIndex={0}
          id={SKIP_TO_CONTENT_IDS.QUESTION_PAGE}
        >
          <Reviewable context={{ type: questionType, id: question.id, title: question.title }}>
            <QuestionTitle appearance="h1">{question.title}</QuestionTitle>
          </Reviewable>
        </QuestionTitleWrapper>
        {this.renderInstructions()}
        <Question
          id={question.id}
          type={question.type}
          isAnswered={question.isAnswered}
          bodyBackground={bodyBackground}
        />
        {this.renderInformationContentOrHints()}
        {this.renderFeedback()}
        <ActionBlock
          question={question}
          goToNextPage={this.navigateToNextPage}
          isNextPageResults={isCourseOnOnePage ? isLastBlock : this.props.checkIsNextPageResults()}
          questionCountInSection={questionCountInSection}
          questionIndex={questionIndex}
          scrollToId={this.props.scrollToId}
          currentScrollLocationId={currentScrollLocationId}
          isLastBlock={isLastBlock}
          isCourseOnOnePage={isCourseOnOnePage}
        />
        {this.renderVoiceOver()}
      </Fragment>
    );
  }

  navigateToNextPage = () => {
    const { navigateToNextPage } = this.props;
    navigateToNextPage();
  };

  getQuestionIndex = () => {
    const {
      questionIndex,
      isCourseOnOnePage,
      isQuestionPool,
      poolQuestionIndex,
      onePageQuestionIndex
    } = this.props;

    if (isCourseOnOnePage) {
      return onePageQuestionIndex + 1;
    }

    if (isQuestionPool) {
      return poolQuestionIndex + 1;
    }

    return questionIndex + 1;
  };

  getTotalQuestionCount = () => {
    const {
      questionCountInSection,
      isCourseOnOnePage,
      questionCountInOnePage,
      isQuestionPool,
      questionPoolSize
    } = this.props;

    if (isCourseOnOnePage) {
      return questionCountInOnePage;
    }

    if (isQuestionPool) {
      return questionPoolSize;
    }

    return questionCountInSection;
  };

  renderQuestionHeader = () => {
    const { question, logoUrl, logoShown, isCourseOnOnePage } = this.props;

    const questionType = question.type === INFORMATION_CONTENT ? INFORMATION_CONTENT : 'question';
    return (
      <React.Fragment>
        {logoShown && !isCourseOnOnePage && <CourseLogo logoUrl={logoUrl} />}
        <PageCounter
          index={this.getQuestionIndex()}
          total={this.getTotalQuestionCount()}
          isCourseOnOnePage={isCourseOnOnePage}
          questionType={questionType}
        />
        <QuestionTitleWrapper tabIndex={0} id={SKIP_TO_CONTENT_IDS.QUESTION_PAGE}>
          <Reviewable context={{ type: questionType, id: question.id, title: question.title }}>
            <QuestionTitle appearance="h1">{question.title}</QuestionTitle>
          </Reviewable>
        </QuestionTitleWrapper>
        {this.renderInstructions()}
      </React.Fragment>
    );
  };

  renderInformationContentOrHints() {
    const { question, showLearningHints } = this.props;
    if (
      question.hints.length === 0 ||
      (!showLearningHints && question.type !== INFORMATION_CONTENT)
    ) {
      return <Separator margin={32} opacity={0.1} color="textColor" />;
    }
    if (question.type === INFORMATION_CONTENT) {
      return <Contents items={question.hints} />;
    }
    return (
      <Reviewable
        context={{
          type: REVIEW_COMMENT_TYPES.QUESTION,
          property: REVIEW_COMMENT_PROPERTIES.LEARNING_CONTENT,
          id: question.id,
          title: question.title
        }}
      >
        <Hint
          items={question.hints}
          shouldRecalculateScrollLocation={this.props.shouldRecalculateScrollLocation}
          isCourseOnOnePage={this.props.isCourseOnOnePage}
        />
      </Reviewable>
    );
  }

  renderFeedback() {
    const { question } = this.props;
    if (question.type === INFORMATION_CONTENT) {
      return null;
    }

    return (
      <Feedback
        question={question}
        animate={true}
        delay={200}
        questionPageRef={this.questionPageRef}
      />
    );
  }

  renderInstructions() {
    const { question } = this.props;
    const { instructions, type } = question;
    return (
      <React.Fragment>
        {instructions.length > 0 && <Contents items={instructions} />}
        {type !== INFORMATION_CONTENT && instructions.length !== 0 && (
          <Separator opacity={0.1} color="textColor" />
        )}
      </React.Fragment>
    );
  }

  renderVoiceOver() {
    const { question, isCourseOnOnePage } = this.props;
    return (
      !!question.voiceOver && (
        <Reviewable
          isVoiceOver={true}
          isCourseOnOnePage={isCourseOnOnePage}
          context={{
            type: REVIEW_COMMENT_TYPES.INFORMATION_CONTENT,
            property: REVIEW_COMMENT_PROPERTIES.VOICE_OVER,
            id: question.id,
            title: question.title
          }}
        >
          <VoiceOver
            tabIndex={0}
            embedCode={question.voiceOver}
            isCourseOnOnePage={isCourseOnOnePage}
          />
        </Reviewable>
      )
    );
  }
}

function mapStateToProps(state: RootAppState, ownProps: any) {
  const isOnePage = isAllQuestionsOnOnePage(state);
  const { sectionId, questionId } = (() => {
    if (isOnePage) {
      return ownProps;
    }
    return ownProps.match.params;
  })();

  return {
    logoUrl: getLogo(state),
    logoShown: isMainLogoShown(state),
    section: getSection(state, sectionId),
    questionIndex: getQuestionIndex(state, sectionId, questionId),
    questionCountInSection: getQuestionCount(state, sectionId),
    question: getQuestion(state, questionId),
    isAnswered: isQuestionAnswered(state, questionId),
    isAnsweredCorrectly: isQuestionAnsweredCorrectly(state, questionId),
    bodyBackground: getBackground(state).body,
    title: getCourseTitle(state),
    sectionExists: sectionExists(state, sectionId),
    questionExists: questionExists(state, questionId),
    questionResponse: getQuestionResponse(state, questionId),
    isQuestionPool: isQuestionPoolEnabled(state),
    questionPoolSize: getQuestionPoolSize(state),
    poolQuestionIndex: getPoolQuestionIndex(state, questionId),
    isCourseOnOnePage: isAllQuestionsOnOnePage(state),
    showLearningHints: isShowLearningHintsEnabled(state)
  };
}

function mapDispatchToProps(dispatch: Dispatch) {
  return { actions: bindActionCreators(questionActions, dispatch) };
}

function predicateForRedirect(props: any) {
  return !props.sectionExists || !props.questionExists;
}

export default withNavigation(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(withFallbackToNotFound(QuestionPage, predicateForRedirect))
);
