import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators, Dispatch } from 'redux';
import withNavigation from 'components/hocs/withNavigation';
import { getCurrentLocationHash } from 'utils/window';
import { getQuestionUrl, getLocationParams, getLearningObjectiveUrl } from 'utils/navigation';
import {
  INDEX_PATH,
  RESULTS_PATH,
  QUESTION_PATH,
  LEARNING_OBJECTIVE_PATH
} from 'constants/navigation';
import { getCourseStructure, isAttemptSubmitted, isPassed } from 'store/course/selectors';
import { getTreeOfContentVisibility } from 'store/treeOfContent/selectors';
import {
  getMasteryScoreValue,
  isOverallMasteryScore,
  isAllQuestionsOnOnePage,
  shouldSubmitAllQuestions,
  isQuestionPoolEnabled,
  getTimerEnabled
} from 'store/settings/selectors';
import { getAffectProgressSectionsCount, isSectionsPassed } from 'store/sections/selectors';
import {
  getAffectProgressQuestionsCount,
  getAnsweredAffectProgressQuestionsCount,
  getAnsweredQuestionsProgress,
  getPoolQuestions
} from 'store/questions/selectors';
import { isLowResolution } from 'store/app/selectors';
import * as actions from 'store/treeOfContent/actions';
import * as courseTimerActions from 'store/timer/actions';
import * as courseQuestionActions from 'store/questions/actions';
import { TimerStatus } from 'constants/timer';
import { localize } from 'core/localization';
import { RootAppState } from 'store/types';
import { getTimerElapsed, getTimerStatus } from 'store/timer/selectors';
import ExpandedToc from './ExpandedToc';
import CollapsedToc from './CollapsedToc';
import { isLearningObjectiveView } from '../shell/RouteTransition/viewChecker';
import { Tail, TailIcon } from './TreeOfContent.styled';

type TreeOfContentProps = {
  isExpanded: boolean;
  courseStructure: {
    title: string;
    sections: { [key: string]: any }[];
    isAttemptSubmitted: boolean;
  };
  isAppInLowResolution: boolean;
  activeSectionId: string;
  activeQuestionId: string;
  navigateToIndex(): void;
  navigateToUrl(url: string): void;
  checkIsNextPageResults(): boolean;
  actions: { [key: string]: any };
  ariaLabel: string;
  isLearningObjectiveView: boolean;
  state: { [key: string]: any };
  masteryScore: number;
  isCourseProgress: boolean;
  affectProgressSectionsCount: number;
  questionsForPool: any;
  isQuestionPool: boolean;
  scrollToId(id: string): void;
  isCourseOnOnePage: boolean;
  shouldSubmitAllQuestions: boolean;
  affectProgressQuestionsCount: number;
  affectAnsweredQuestionsCount: number;
  answeredQuestionsProgress: number;
  showSubmitButton: boolean;
  isSectionsPassed: boolean;
  isCoursePassed: boolean;
  timerActions: { [key: string]: any };
  questionActions: { [key: string]: any };
  isTimerEnabled: boolean;
  timerStatus: TimerStatus;
  timeElapsed: number;
};

export class TreeOfContent extends Component<TreeOfContentProps, {}> {
  navigateToQuestion = (sectionId: string, questionId: string) => {
    const {
      isTimerEnabled,
      timerActions,
      questionActions,
      timeElapsed,
      isCourseOnOnePage,
      timerStatus
    } = this.props;

    if (
      isTimerEnabled &&
      (timerStatus === TimerStatus.NOT_STARTED || timerStatus === TimerStatus.RESET)
    ) {
      timerActions.startTimer();
      timerActions.setTimerElapsed(timeElapsed);
    }
    if (isCourseOnOnePage) {
      this.navigate(getQuestionUrl(sectionId, questionId));
      this.scrollNavigate(questionId);
      questionActions.markAsOpened(questionId);
    } else {
      this.navigate(getQuestionUrl(sectionId, questionId));
    }
  };

  navigateToResults = () => {
    this.navigate(RESULTS_PATH);
  };

  navigateToIndex = () => {
    this.navigate(INDEX_PATH);
  };

  navigateToLearningObjective = (sectionId: string, isCourseOnOnePage: boolean) => {
    if (isCourseOnOnePage) {
      this.navigate(getLearningObjectiveUrl(sectionId));
      this.scrollNavigate(sectionId);
    } else {
      this.navigate(getLearningObjectiveUrl(sectionId));
    }
  };

  submitAllAnswerOnce = (section: any) => {
    const { isTimerEnabled, timerActions } = this.props;
    if (timerActions && isTimerEnabled) {
      timerActions.setTimerStatus(TimerStatus.STOPPED);
      timerActions.updateTimerProgress();
    }
    this.props.actions.submitAllAnswerOnce(section);
  };

  navigate = (url: string) => {
    if (this.props.isAppInLowResolution) {
      this.toggleTreeOfContentVisibility();
    }
    this.props.navigateToUrl(url);
  };

  scrollNavigate = (id: string) => {
    if (this.props.isAppInLowResolution) {
      this.toggleTreeOfContentVisibility();
    }
    this.props.scrollToId(id);
  };

  toggleTreeOfContentVisibility = () => {
    this.props.actions.toggleTreeOfContentVisibility();
  };

  render() {
    const { isExpanded } = this.props;
    const tocProps = {
      courseStructure: this.props.courseStructure,
      activeSectionId: this.props.activeSectionId,
      activeQuestionId: this.props.activeQuestionId,
      navigateToIndex: this.navigateToIndex,
      navigateToResults: this.navigateToResults,
      navigateToQuestion: this.navigateToQuestion,
      navigateToLearningObjective: this.navigateToLearningObjective,
      submitAllAnswerOnce: this.submitAllAnswerOnce,
      isResultsActive: !this.props.activeSectionId,
      isExpanded,
      isLearningObjectiveView: this.props.isLearningObjectiveView,
      courseProgress: this.props.state.course.score,
      masteryScore: this.props.masteryScore,
      isCourseProgress: this.props.isCourseProgress,
      affectProgressSectionsCount: this.props.affectProgressSectionsCount,
      questionsForPool: this.props.questionsForPool,
      isQuestionPool: this.props.isQuestionPool,
      isCourseOnOnePage: this.props.isCourseOnOnePage,
      shouldSubmitAllQuestions: this.props.shouldSubmitAllQuestions,
      affectProgressQuestionsCount: this.props.affectProgressQuestionsCount,
      affectAnsweredQuestionsCount: this.props.affectAnsweredQuestionsCount,
      answeredQuestionsProgress: this.props.answeredQuestionsProgress,
      showSubmitButton: this.props.showSubmitButton,
      isNextPageResults: this.props.checkIsNextPageResults(),
      isCoursePassed: this.props.isCoursePassed
    };
    return (
      <React.Fragment>
        <ExpandedToc {...tocProps} />
        <Tail
          isExpanded={isExpanded}
          onClick={this.toggleTreeOfContentVisibility}
          tabIndex={0}
          ariaLabel={localize(
            isExpanded ? '[hamburger aria label expanded]' : '[hamburger aria label collapse]'
          )}
        >
          <TailIcon name="hamburger" size={12} />
        </Tail>
        <CollapsedToc {...tocProps} />
      </React.Fragment>
    );
  }
}

function mapStateToProps(state: RootAppState) {
  const params = getLocationParams(QUESTION_PATH);
  const learningObjectiveParams = getLocationParams(LEARNING_OBJECTIVE_PATH);
  return {
    isExpanded: getTreeOfContentVisibility(state),
    courseStructure: getCourseStructure(state),
    isAppInLowResolution: isLowResolution(state),
    isLearningObjectiveView: isLearningObjectiveView.test(
      getCurrentLocationHash().replace('#', '')
    ),
    activeSectionId: params.sectionId || learningObjectiveParams.sectionId,
    activeQuestionId: params.questionId,
    masteryScore: getMasteryScoreValue(state),
    isCourseProgress: isOverallMasteryScore(state),
    affectProgressSectionsCount: getAffectProgressSectionsCount(state),
    questionsForPool: getPoolQuestions(state),
    isQuestionPool: isQuestionPoolEnabled(state),
    isCourseOnOnePage: isAllQuestionsOnOnePage(state),
    shouldSubmitAllQuestions: shouldSubmitAllQuestions(state),
    affectProgressQuestionsCount: getAffectProgressQuestionsCount(state),
    affectAnsweredQuestionsCount: getAnsweredAffectProgressQuestionsCount(state),
    answeredQuestionsProgress: getAnsweredQuestionsProgress(state),
    showSubmitButton: !isAttemptSubmitted(state),
    isSectionsPassed: isSectionsPassed(state),
    isCoursePassed: isPassed(state),
    timerStatus: getTimerStatus(state),
    isTimerEnabled: getTimerEnabled(state),
    timeElapsed: getTimerElapsed(state)
  };
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch),
    timerActions: bindActionCreators(courseTimerActions, dispatch),
    questionActions: bindActionCreators(courseQuestionActions, dispatch)
  };
}

export default withRouter(
  withNavigation(connect(mapStateToProps, mapDispatchToProps)(TreeOfContent))
);
