import React from 'react';
import { SIZES, ICON } from 'constants/components';
import { transparentize } from 'utils/color';
import { localize } from 'core/localization';
import { NavigationItemIcon } from './NavigationItem.styled';

type NavigationLearningItemProps = {
  sectionTitle?: string;
  onClick(e?: any): void;
  onKeyDown(e?: any): void;
  isActive: boolean;
  role: string;
  tabIndex: number;
};
export const NavigationLearningItem = ({
  sectionTitle,
  onClick,
  onKeyDown,
  isActive,
  role,
  tabIndex
}: NavigationLearningItemProps) =>
  getNavigationItemIcon({
    ariaLabel: sectionTitle,
    title: sectionTitle,
    isActive,
    onClick,
    onKeyDown,
    size: SIZES.MEDIUM,
    role,
    tabIndex,
    ...getContentIconProps(isActive)
  });

type NavigationItemProps = {
  question: {
    title: string;
    isInformationContent: boolean;
    isAnswered: boolean;
    isAnsweredCorrectly: boolean;
    isSurvey: boolean;
  };
  isActive: boolean;
  onClick?(e?: any): void;
  onKeyDown?(e?: any): void;
  role: string;
  tabIndex: number;
  shouldSubmitAllQuestions: boolean;
  isAttemptSubmitted: boolean;
};
export const NavigationItem = ({
  question,
  onClick,
  onKeyDown,
  isActive,
  role,
  tabIndex,
  shouldSubmitAllQuestions,
  isAttemptSubmitted
}: NavigationItemProps) => {
  let iconProps = getQuestionIconProps(isActive, question);
  if (question.isInformationContent) {
    iconProps = getContentIconProps(isActive);
  } else if (shouldSubmitAllQuestions) {
    iconProps = getSubmitOnceQuestionIconProps(isActive, question, isAttemptSubmitted);
  }

  const ariaLabel = question.isAnswered
    ? question.isAnsweredCorrectly
      ? `${localize('[aria label question icon ok]')} ${question.title}`
      : `${localize('[aria label question icon non]')} ${question.title}`
    : `${localize('[aria label question icon]')} ${question.title}`;
  return getNavigationItemIcon({
    ariaLabel: question.isInformationContent ? question.title : ariaLabel,
    title: question.title || ariaLabel,
    isActive,
    onClick,
    onKeyDown,
    size: SIZES.MEDIUM,
    role,
    tabIndex,
    ...iconProps
  });
};

function getNavigationItemIcon(configs: any) {
  return <NavigationItemIcon {...configs} />;
}

function getContentIconProps(isActive: boolean) {
  return {
    name: 'point',
    fontSize: 4,
    shape: isActive ? ICON.SHAPES.ROUND_SHADED : ICON.SHAPES.ROUND_FULL_COLOR,
    getColor: (colors: any) => getDefaultQuestionColor(isActive, colors),
    getShapeColor: (colors: any) => transparentize(colors.textColor, 0.1),
    ...(!isActive && {
      getHoverColor: (colors: any) => colors.textColor,
      hoverShape: ICON.SHAPES.ROUND_SHADED
    })
  };
}

function getSubmitOnceQuestionIconProps(
  isActive: boolean,
  question: any,
  isAttemptSubmitted: boolean
) {
  return {
    name: getIconName(question, isAttemptSubmitted),
    fontSize: question.isAnswered ? 5 : 8,
    getColor: (colors: any) =>
      question.isAnswered && isAttemptSubmitted
        ? getAnsweredQuestionColor(question, colors)
        : transparentize(colors.textColor, 0.5),
    getShapeColor: (colors: any) => getSubmitOnceShapeColor(question, colors, isAttemptSubmitted),
    shape: isActive
      ? ICON.SHAPES.ROUND_SHADED
      : question.isAnswered
      ? ICON.SHAPES.ROUND_OUTLINED
      : ICON.SHAPES.ROUND_FULL_COLOR,
    ...(!isActive && {
      hoverShape: ICON.SHAPES.ROUND_SHADED
    }),
    ...(!isActive &&
      !question.isAnswered && {
        getHoverColor: (colors: any) => colors.textColor
      })
  };
}

function getQuestionIconProps(isActive: boolean, question: any) {
  let name = question.isAnswered ? (question.isAnsweredCorrectly ? 'ok' : 'close') : 'question';
  if (question.isSurvey) {
    name = 'survey';
  }
  let shape: string = ICON.SHAPES.ROUND_FULL_COLOR;
  if (isActive) {
    shape = ICON.SHAPES.ROUND_SHADED
  } else if (question.isAnswered){
    shape = ICON.SHAPES.ROUND_OUTLINED
  }

  return {
    name,
    fontSize: question.isAnswered ? 5 : 8,
    getColor: (colors: any) =>
      question.isAnswered
        ? getAnsweredQuestionColor(question, colors)
        : getDefaultQuestionColor(isActive, colors),
    getShapeColor: (colors: any) => getShapeColor(question, colors),
    shape,
    ...(!isActive && {
      hoverShape: ICON.SHAPES.ROUND_SHADED
    }),
    ...(!isActive &&
      !question.isAnswered && {
        getHoverColor: (colors: any) => colors.textColor
      })
  };
}

function getIconName(question: any, isAttemptSubmitted: boolean) {
  return question.isSurvey
    ? 'survey'
    : getAnsweredOnceIconName(
        question.isAnswered,
        isAttemptSubmitted,
        question.isAnsweredCorrectly,
        question.isSurvey
      );
}

function getAnsweredOnceIconName(
  isAnswered: boolean,
  isAttemptSubmitted: boolean,
  isAnsweredCorrectly: boolean,
  isSurvey: boolean
) {
  if (isAnswered && isAttemptSubmitted && !(isAnsweredCorrectly || isSurvey)) {
    return 'close';
  } else if (isAnswered || isAnsweredCorrectly || isSurvey) {
    return 'ok';
  } else {
    return 'question';
  }
}

function getShapeColor(question: any, colors: any) {
  return question.isAnswered
    ? getAnsweredQuestionColor(question, colors)
    : transparentize(colors.textColor, 0.1);
}

function getSubmitOnceShapeColor(question: any, colors: any, isAttemptSubmitted: boolean) {
  return question.isAnswered && isAttemptSubmitted
    ? getAnsweredQuestionColor(question, colors)
    : transparentize(colors.textColor, 0.1);
}
function getAnsweredQuestionColor(question: any, colors: any) {
  return question.isAnsweredCorrectly || question.isSurvey
    ? colors.correctColor
    : colors.incorrectColor;
}

function getDefaultQuestionColor(isActive: any, colors: any) {
  return isActive ? colors.mainColor : transparentize(colors.textColor, 0.5);
}

export default NavigationItem;
