import * as React from 'react';
import { SIZES, ICON } from 'constants/components';
import { INFORMATION_CONTENT } from 'constants/questionTypes';
import QuestionTreeNode, { QuestionTreeNodeProps } from './QuestionTreeNode';
import { QuestionItem, TitleText, QuestionIcon } from './QuestionTreeNode.styled';

interface QuestionTreeNodeSubmitOnceProps extends QuestionTreeNodeProps {
  isAttemptSubmitted?: boolean;
}

export default class QuestionTreeNodeSubmitOnce extends QuestionTreeNode<
  QuestionTreeNodeSubmitOnceProps
> {
  render() {
    const {
      question,
      onClick,
      isActive,
      onKeyDown,
      role,
      tabIndex,
      isAttemptSubmitted = false
    } = this.props;
    const {
      title,
      isInformationContent,
      isAnswered,
      isAnsweredCorrectly,
      isSurvey,
      type,
      hasBeenOpened
    } = question;
    const isPassed = type === INFORMATION_CONTENT ? hasBeenOpened : isAnswered;
    return (
      <QuestionItem
        isActive={isActive}
        isPassed={isPassed}
        tabIndex={tabIndex}
        role={role}
        ariaLabel={
          !isInformationContent
            ? this.getTitleQuestion(isAnswered, isAnsweredCorrectly, title)
            : title
        }
        {...(!isActive && { onClick, onKeyDown })}
      >
        {!isInformationContent &&
          renderQuestionIcon(isAnswered, isAnsweredCorrectly, isSurvey, isAttemptSubmitted)}
        <TitleText>{title}</TitleText>
      </QuestionItem>
    );
  }
}

function getIconName(
  isAttemptSubmitted: boolean,
  isAnsweredCorrectly: boolean,
  isSurvey: boolean,
  isAnswered: boolean
) {
  let questionIconText = '';

  if (isSurvey) {
    questionIconText = 'survey';
  } else if (!isAttemptSubmitted && isAnswered) {
    questionIconText = 'ok';
  } else if (isAttemptSubmitted && isAnswered) {
    if (isAnsweredCorrectly) {
      questionIconText = 'ok';
    } else {
      questionIconText = 'close';
    }
  } else {
    questionIconText = 'question';
  }
  return questionIconText;
}

function getShapeColor(
  isAttemptSubmitted: boolean,
  isAnsweredCorrectly: boolean,
  isSurvey: boolean
) {
  let questionTextColor = 'textColor';
  if (isAttemptSubmitted) {
    if (isAnsweredCorrectly || isSurvey) {
      questionTextColor = 'correctColor';
    } else {
      questionTextColor = 'incorrectColor';
    }
  }
  return questionTextColor;
}

function renderQuestionIcon(
  isAnswered: boolean,
  isAnsweredCorrectly: boolean,
  isSurvey: boolean,
  isAttemptSubmitted = false
) {
  const questionIconText = getIconName(
    isAttemptSubmitted,
    isAnsweredCorrectly,
    isSurvey,
    isAnswered
  );
  const questionTextColor = getShapeColor(isAttemptSubmitted, isAnsweredCorrectly, isSurvey);

  return (
    <QuestionIcon
      name={questionIconText}
      size={SIZES.SMALL}
      shape={ICON.SHAPES.ROUND_FULL_COLOR}
      shapeColor={questionTextColor}
      iconColor={!isAttemptSubmitted && questionTextColor}
      shapeOpacity={!isAttemptSubmitted && 0.08}
      {...(!isAnswered && { fontSize: 7 })}
    />
  );
}
