import * as React from 'react';
import { connect } from 'react-redux';
import { isPassed } from 'utils/comparison';
import { Text, Separator } from 'components/common';
import {
  getCourseTitle,
  getCourseIntroductions,
  getScore,
  isAttemptSubmitted
} from 'store/course/selectors';
import {
  getMasteryScoreValue,
  isOverallMasteryScore,
  getAlternativeLogo,
  shouldSubmitAllQuestions,
  getTimerEnabled,
  getTimerLimit,
  isQuestionPoolEnabled
} from 'store/settings/selectors';
import {
  getAffectProgressSectionsCount,
  getSectionsPassedCount,
  getAnsweredSectionsCount
} from 'store/sections/selectors';
import {
  getAffectProgressQuestionsCount,
  getAnsweredAffectProgressQuestionsCount,
  getAnsweredQuestionsProgress
} from 'store/questions/selectors';
import Contents from 'components/contents/Contents';
import { localize } from 'core/localization';
import Reviewable from 'components/review/Reviewable';
import {
  HINT_FOR_SPOT_COMMENT,
  REVIEW_COMMENT_PROPERTIES,
  REVIEW_COMMENT_TYPES
} from 'constants/review';
import { RootAppState } from 'store/types';
import { getTimeFromMilliseconds } from 'utils/timer';
import {
  Container,
  CourseInfoContainer,
  CourseInfoWrapper,
  ContentsContainer,
  LogoWrapper,
  CourseLogo,
  ProgressSectionsWrapper,
  ProgressPerSection,
  ProgressMasteryScore,
  ProgressCaption,
  TimerInfoWrapper,
  TimerIcon
} from './CourseInformation.styled';

type CourseInformationProps = {
  title?: string;
  introductions?: Array<string>;
  sections: Array<any>;
  courseProgress?: number;
  masteryScore?: number;
  isCourseProgress?: boolean;
  affectProgressSectionsCount?: number;
  answeredSectionsCount?: number;
  sectionsPassedCount?: number;
  reviewMode?: boolean;
  alternativeLogoUrl: string;
  isSubmitAtOnce: boolean;
  affectProgressQuestionsCount: number;
  affectAnsweredQuestionsCount: number;
  answeredQuestionsProgress: number;
  isCourseSubmitted?: boolean;
  isTimerEnabled: boolean;
  timeLimit: number;
  isQuestionPool: boolean;
};

export class CourseInformation extends React.Component<CourseInformationProps, {}> {
  refContainer: any;

  constructor(props: CourseInformationProps) {
    super(props);
    this.refContainer = React.createRef();
  }

  renderCourseTimerInfo = (timeLimit: number) => (
    <TimerInfoWrapper>
      <TimerIcon size={18} name="timer" />
      <Text title={'title'} appearance="p" inverted data-test="course-timer-text">
        {`${localize('[timer]')}: ${timeLimit} ${localize('[minutes]')}`}
      </Text>
    </TimerInfoWrapper>
  );

  renderProgressCaption = (
    courseProgress: number,
    affectAnsweredQuestionsCount: number,
    affectProgressQuestionsCount: number,
    isSubmitAtOnce = false,
    isCourseSubmitted = false
  ) => {
    if (isCourseSubmitted) {
      return localize('[you complete x]', {
        percentSuccess: courseProgress
      });
    }
    if (isSubmitAtOnce) {
      return localize('[x out of y questions answered]', {
        countAnsweredQuestions: affectAnsweredQuestionsCount,
        countQuestions: affectProgressQuestionsCount
      });
    } else {
      return localize('[you complete x]', {
        percentSuccess: courseProgress
      });
    }
  };

  getProgress = (): number => {
    const {
      isSubmitAtOnce,
      courseProgress,
      answeredQuestionsProgress,
      isCourseSubmitted
    } = this.props;
    if (isCourseSubmitted) {
      return courseProgress as number;
    }
    return isSubmitAtOnce ? answeredQuestionsProgress : (courseProgress as number);
  };

  getBackgroundSettings = () => {
    const { isSubmitAtOnce, isCourseSubmitted } = this.props;

    if (isCourseSubmitted) {
      return {
        opacity: 1.0,
        progressColor: 'correctColor'
      };
    }
    if (isSubmitAtOnce) {
      return {
        opacity: 0.3,
        progressColor: 'coverBgOverlayTextColor'
      };
    } else {
      return {
        opacity: 1.0,
        progressColor: 'correctColor'
      };
    }
  };

  renderMasteryScoreSectionProgress = (
    isSubmitAtOnce: boolean,
    courseProgress: number,
    answeredQuestionsProgress: number,
    masteryScore: number,
    isCourseSubmitted = false
  ) => (
    <ProgressMasteryScore
      forwardedRef={this.refContainer}
      progress={this.getProgress()}
      passedMark={masteryScore}
      {...this.getBackgroundSettings()}
    />
  );

  render() {
    const {
      title,
      introductions,
      alternativeLogoUrl,
      sections,
      courseProgress = 0,
      masteryScore = 0,
      isCourseProgress,
      sectionsPassedCount,
      affectProgressSectionsCount = 0,
      answeredSectionsCount = 0,
      isSubmitAtOnce,
      affectProgressQuestionsCount,
      affectAnsweredQuestionsCount = 0,
      answeredQuestionsProgress,
      isCourseSubmitted,
      isTimerEnabled,
      timeLimit,
      isQuestionPool
    } = this.props;

    return (
      <Container ref={this.refContainer}>
        <CourseInfoContainer>
          <CourseInfoWrapper>
            <LogoWrapper>
              <CourseLogo logoUrl={alternativeLogoUrl} alt={localize('[alt logo login]')} />
            </LogoWrapper>
            <Reviewable
              context={{
                type: REVIEW_COMMENT_TYPES.COURSE,
                property: REVIEW_COMMENT_PROPERTIES.TITLE
              }}
              hintName={HINT_FOR_SPOT_COMMENT}
            >
              <Text title={title} appearance="h1" inverted tabIndex={-1} ignoreFocus={true}>
                {title}
              </Text>
            </Reviewable>
            {affectProgressSectionsCount > 0 ? (
              <>
                {isCourseProgress && (
                  <>
                    {this.renderMasteryScoreSectionProgress(
                      isSubmitAtOnce,
                      courseProgress,
                      answeredQuestionsProgress,
                      masteryScore,
                      isCourseSubmitted
                    )}
                    <ProgressCaption data-test="progress-caption-text">
                      {this.renderProgressCaption(
                        courseProgress,
                        affectAnsweredQuestionsCount,
                        affectProgressQuestionsCount,
                        isSubmitAtOnce,
                        isCourseSubmitted
                      )}
                    </ProgressCaption>
                  </>
                )}
                {!isCourseProgress && (
                  <>
                    {renderSectionProgress(sections, isSubmitAtOnce, isCourseSubmitted)}
                    <ProgressCaption data-test="progress-caption-text">
                      {isSubmitAtOnce &&
                        isCourseSubmitted &&
                        localize('[x out of y sections passed]', {
                          countSectionsSuccess: sectionsPassedCount,
                          countSections: affectProgressSectionsCount
                        })}
                      {isSubmitAtOnce &&
                        !isCourseSubmitted &&
                        localize('[x out of y sections taken]', {
                          countSectionsSuccess: answeredSectionsCount,
                          countSections: affectProgressSectionsCount
                        })}
                      {!isSubmitAtOnce &&
                        localize('[x out of y sections passed]', {
                          countSectionsSuccess: sectionsPassedCount,
                          countSections: affectProgressSectionsCount
                        })}
                    </ProgressCaption>
                  </>
                )}
              </>
            ) : (
              <Separator height={2} opacity={0.3} margin={56} color="coverBgOverlayTextColor" />
            )}
            {isTimerEnabled && !isQuestionPool && this.renderCourseTimerInfo(timeLimit)}
            {renderCourseIntroduction(introductions)}
          </CourseInfoWrapper>
        </CourseInfoContainer>
      </Container>
    );
  }
}

function renderSectionProgress(sections: any, isSubmitAtOnce: boolean, isCourseSubmitted = false) {
  return (
    <ProgressSectionsWrapper>
      {sections.map((section: any) => {
        if (section.affectsProgress) {
          const { id } = section;
          if (isPassed(section.status)) {
            return isSubmitAtOnce ? (
              renderSubmitAtOnceProgressPerSection(isCourseSubmitted, section, id)
            ) : (
              <ProgressPerSection key={id} progress={100} />
            );
          } else {
            return isSubmitAtOnce ? (
              renderSubmitAtOnceProgressPerSection(isCourseSubmitted, section, id)
            ) : (
              <ProgressPerSection key={id} progress={0} />
            );
          }
        }
        return false;
      })}
    </ProgressSectionsWrapper>
  );
}

function renderSubmitAtOnceProgressPerSection(isCourseSubmitted: boolean, section: any, id: any) {
  if (isCourseSubmitted) {
    if (isPassed(section.status)) {
      return <ProgressPerSection key={id} progress={100} />;
    }
    return <ProgressPerSection key={id} progress={0} />;
  } else {
    if (section.isSectionAnswered) {
      return (
        <ProgressPerSection
          key={id}
          progress={100}
          progressColor="coverBgOverlayTextColor"
          opacity={0.4}
        />
      );
    }
    return <ProgressPerSection key={id} progress={0} />;
  }
}

function renderCourseIntroduction(introductions: any) {
  return (
    Array.isArray(introductions) &&
    introductions.length !== 0 && (
      <React.Fragment>
        <Reviewable
          context={{
            type: REVIEW_COMMENT_TYPES.COURSE,
            property: REVIEW_COMMENT_PROPERTIES.INTRODUCTION
          }}
        >
          <ContentsContainer>
            <Contents inverted items={introductions} linkTabIndex={0} />
          </ContentsContainer>
        </Reviewable>
      </React.Fragment>
    )
  );
}

function mapStateToProps(state: RootAppState) {
  return {
    alternativeLogoUrl: getAlternativeLogo(state),
    title: getCourseTitle(state),
    introductions: getCourseIntroductions(state),
    courseProgress: getScore(state),
    masteryScore: getMasteryScoreValue(state),
    isCourseProgress: isOverallMasteryScore(state),
    affectProgressSectionsCount: getAffectProgressSectionsCount(state),
    sectionsPassedCount: getSectionsPassedCount(state),
    answeredSectionsCount: getAnsweredSectionsCount(state),
    affectProgressQuestionsCount: getAffectProgressQuestionsCount(state),
    affectAnsweredQuestionsCount: getAnsweredAffectProgressQuestionsCount(state),
    answeredQuestionsProgress: getAnsweredQuestionsProgress(state),
    isSubmitAtOnce: shouldSubmitAllQuestions(state),
    isCourseSubmitted: isAttemptSubmitted(state),
    isTimerEnabled: getTimerEnabled(state),
    timeLimit: getTimeFromMilliseconds(getTimerLimit(state), 'min') as number,
    isQuestionPool: isQuestionPoolEnabled(state)
  };
}
export default connect(mapStateToProps)(CourseInformation);
