import * as React from 'react';
import { connect } from 'react-redux';
import { getProgressStatusLocalizationKey } from 'utils/localization';
import { isPassed, isFailed } from 'utils/comparison';
import { Collapse, Icon } from 'components/common';
import { localize } from 'core/localization';
import ProgressPopover from 'components/common/ProgressPopover';
import {
  getAffectProgressQuestionsCountInSection,
  getAnsweredSectionQuestionsCount,
  getCorrectlyAffectProgressQuestionsCountInSection
} from 'store/sections/selectors';
import { shouldSubmitAllQuestions } from 'store/settings/selectors';
import { isAttemptSubmitted } from 'store/course/selectors';
import { RootAppState } from 'store/types';

import {
  SectionText,
  SectionItem,
  Header,
  TitleIcon,
  Footer,
  Status,
  StatusIcon,
  StatusText,
  QuestionList,
  ProgressPopoverWrapper
} from './SectionTreeNode.styled';

type SectionTreeNodeProps = {
  isActive: boolean;
  section: {
    title: string;
    status: number;
    affectsProgress?: boolean;
    score: number;
    id: string;
  };
  children: any[];
  ariaLabel?: string;
  tabIndex: number;
  questions: any[];
  isSubmitAtOnce?: boolean;
  affectProgressQuestionsCountInSection?: number;
  answeredSectionQuestionsCount?: number;
  correctlyAffectProgressQuestionsCountInSection?: number;
  isCourseSubmitted?: boolean;
  isCourseProgress: boolean;
  sectionIndex: any;
};

type SectionTreeNodeState = {
  isExpanded: boolean;
};

export class SectionTreeNode extends React.Component<SectionTreeNodeProps, SectionTreeNodeState> {
  constructor(props: SectionTreeNodeProps) {
    super(props);
    this.state = {
      isExpanded: this.props.isActive
    };
  }

  componentDidUpdate(prevProps: SectionTreeNodeProps) {
    if (this.props.isActive !== prevProps.isActive) {
      this.setState({
        isExpanded: this.props.isActive
      });
    }
  }

  toggleIsExpanded = () => {
    this.setState(prevState => ({
      isExpanded: !prevState.isExpanded
    }));
  };

  renderSubmitOnceSectionProgress = (
    section: {
      title: string;
      status: number;
    },
    isCourseSubmitted: boolean,
    isCourseProgress: boolean,
    sectionStatusId: string,
    correctlyAffectProgressQuestionsCountInSection?: number,
    affectProgressQuestionsCountInSection?: number,
    answeredSectionQuestionsCount?: number
  ) => {
    const { status, title } = section;
    const isSectionPassed = isPassed(status);
    const statusText = getProgressStatusLocalizationKey(status);
    return (
      <React.Fragment>
        <Status>
          {isSectionPassed && isCourseSubmitted && !isCourseProgress && (
            <StatusIcon>
              <Icon name="ok" size={5} color="correctColor" />
            </StatusIcon>
          )}
          {isFailed(status) && isCourseSubmitted && !isCourseProgress && (
            <StatusIcon>
              <Icon name="close" size={5} color="textColor" opacity={0.4} />
            </StatusIcon>
          )}
          <StatusText
            isPassed={isSectionPassed && isCourseSubmitted}
            ellipsis
            ariaLabel={`${localize('[aria label status text]')} ${title} ${localize(statusText)}`}
            tabIndex={0}
            role="status"
            data-test="question-answered-text"
            isOverallMasteryScore={isCourseProgress}
            id={sectionStatusId}
          >
            {isCourseSubmitted &&
              isCourseProgress &&
              localize('[x y answered correctly]', {
                countCorrectlyAnsweredQuestionsInSection: correctlyAffectProgressQuestionsCountInSection,
                countQuestionsInSection: affectProgressQuestionsCountInSection
              })}
            {isCourseSubmitted && !isCourseProgress && localize(statusText)}
            {!isCourseSubmitted &&
              localize('[x y questions answered]', {
                countAnsweredQuestionsInSection: answeredSectionQuestionsCount,
                countQuestionsInSection: affectProgressQuestionsCountInSection
              })}
          </StatusText>
        </Status>
      </React.Fragment>
    );
  };

  renderSectionProgress = (
    section: {
      title: string;
      status: number;
    },
    isCourseProgress: boolean,
    sectionStatusId: string,
    correctlyAffectProgressQuestionsCountInSection?: number,
    affectProgressQuestionsCountInSection?: number
  ) => {
    const { status, title } = section;
    const isSectionPassed = isPassed(status);
    const statusText = getProgressStatusLocalizationKey(status);
    return (
      <>
        <Status>
          {isSectionPassed && (
            <StatusIcon>
              <Icon name="ok" size={5} color="correctColor" />
            </StatusIcon>
          )}
          <StatusText
            data-test="question-answered-text"
            isPassed={isSectionPassed}
            ellipsis
            ariaLabel={`${localize('[aria label status text]')} ${title} ${localize(statusText)}`}
            tabIndex={-1}
            role="status"
            isOverallMasteryScore={isCourseProgress}
            id={sectionStatusId}
          >
            {isCourseProgress
              ? localize('[x y answered correctly]', {
                  countCorrectlyAnsweredQuestionsInSection: correctlyAffectProgressQuestionsCountInSection,
                  countQuestionsInSection: affectProgressQuestionsCountInSection
                })
              : localize(statusText)}
          </StatusText>
        </Status>
      </>
    );
  };

  render() {
    const {
      section,
      children,
      tabIndex,
      questions,
      isSubmitAtOnce,
      affectProgressQuestionsCountInSection,
      answeredSectionQuestionsCount,
      correctlyAffectProgressQuestionsCountInSection,
      isCourseSubmitted = false,
      isCourseProgress,
      sectionIndex
    } = this.props;
    const { id, affectsProgress, title, status, score } = section;
    const { isExpanded } = this.state;
    const sectionStatusId = `section-status-${sectionIndex}`;
    return (
      <SectionItem role="presentation">
        <Header
          ariaLabel={
            isExpanded
              ? `${localize('[close section items]')} ${title}`
              : `${localize('[open section items]')} ${title}`
          }
          tabIndex={tabIndex}
          role="menuitem"
          ariaExpanded={true}
          aria-haspopup={children.length > 0 ? 'true' : 'false'}
          onClick={this.toggleIsExpanded}
          onKeyDown={event => {
            if (event.key === 'Enter') {
              this.toggleIsExpanded();
            }
          }}
        >
          <SectionText appearance="h4" maxLength={35}>
            {title}
          </SectionText>
          <TitleIcon size={5} name={isExpanded ? 'chevron-up' : 'chevron-down'} />
        </Header>
        <Collapse
          isExpanded={isExpanded}
          animateAppear={false}
          duration={200}
          opacityDuration="200"
        >
          <QuestionList role="menu">{children}</QuestionList>
        </Collapse>
        <Footer>
          {affectsProgress && (
            <ProgressPopoverWrapper>
              <ProgressPopover
                popoverPosition={'top'}
                questions={questions}
                score={score}
                status={status}
                sectionId={id}
                ariaLabelledby={sectionStatusId}
              />
              {isSubmitAtOnce
                ? this.renderSubmitOnceSectionProgress(
                    section,
                    isCourseSubmitted,
                    isCourseProgress,
                    sectionStatusId,
                    correctlyAffectProgressQuestionsCountInSection,
                    affectProgressQuestionsCountInSection,
                    answeredSectionQuestionsCount
                  )
                : this.renderSectionProgress(
                    section,
                    isCourseProgress,
                    sectionStatusId,
                    correctlyAffectProgressQuestionsCountInSection,
                    affectProgressQuestionsCountInSection
                  )}
            </ProgressPopoverWrapper>
          )}
        </Footer>
      </SectionItem>
    );
  }
}

function mapStateToProps(state: RootAppState, ownProps: SectionTreeNodeProps) {
  return {
    affectProgressQuestionsCountInSection: getAffectProgressQuestionsCountInSection(
      state,
      ownProps.section.id
    ),
    answeredSectionQuestionsCount: getAnsweredSectionQuestionsCount(state, ownProps.section.id),
    correctlyAffectProgressQuestionsCountInSection: getCorrectlyAffectProgressQuestionsCountInSection(
      state,
      ownProps.section.id
    ),
    isSubmitAtOnce: shouldSubmitAllQuestions(state),
    isCourseSubmitted: isAttemptSubmitted(state)
  };
}
export default connect(mapStateToProps)(SectionTreeNode);
