import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { Route, Switch } from 'react-router';
import { ThemeProvider } from 'styled-components';
import { bindActionCreators, Dispatch } from 'redux';
import { isProgressRestoreFailed } from 'store/course/selectors';
import { getTimerStatus } from 'store/timer/selectors';
import { TimerStatus } from 'constants/timer';
import { RootAppState } from 'store/types';
import { MATERIAL_TYPES } from 'constants/common';
import TimedOut from 'components/popups/timedOut';
import RouteTransition from '../../components/shell/RouteTransition/RouteTransition';
import Layout from '../../components/shell/Layout/Layout';
import { PrivateRoute, RouteWithRedirect } from '../../components/common';
import {
  INDEX_PATH,
  LOGIN_PATH,
  RESULTS_PATH,
  SECTION_PATH,
  SECTIONS_PATH,
  SIGNUP_PATH
} from '../../constants/navigation';
import Sections from './sections/SectionsPage';
import NoAccess from '../errors/noAccess/LimitAccessPage';
import CourseOverview from './courseOverview/CourseOverview';
import Login from '../login/LoginPage';
import SignUp from '../signUp/SignUpPage';
import NotFoundPage from '../errors/notFound/NotFoundPage';
import * as courseActions from '../../store/course/actions';
import { IS_NO_ACCESS, IS_NOT_AUTHORIZED, IS_SUCCESSFUL_ACCESS } from '../../constants/typeAccess';
import { POPUP_ID_START_OVER, POPUP_ID_TIMED_OUT } from '../../constants/popups';
import StartOverPopup from '../../components/popups/startOver/StartOver';
import * as popupActions from '../../store/popup/actions';
import ErrorBoundary from '../../components/errorBoundary/ErrorBoundary';

type CourseProps = {
  colors: { [key: string]: any };
  fonts: { [key: string]: any };
  background: { [key: string]: any };
  isLowResolution: boolean;
  isTocExpanded: boolean;
  isUserAuthenticated: boolean;
  location: { [key: string]: any };
  navigateToUrl(url: string): void;
  courseActions: { [key: string]: any };
  userAccessIsLimited: boolean;
  navigateToIndex(): void;
  popupActions: { [key: string]: any };
  isCourseProgressRestoreFailed?: boolean;
  timerStatus: TimerStatus;
};

const Course = (props: CourseProps) => {
  const {
    colors,
    fonts,
    background,
    isLowResolution,
    isTocExpanded,
    isUserAuthenticated,
    userAccessIsLimited,
    isCourseProgressRestoreFailed,
    timerStatus
  } = props;
  let typeAccess = IS_SUCCESSFUL_ACCESS;

  if (userAccessIsLimited || !isUserAuthenticated) {
    typeAccess = userAccessIsLimited && isUserAuthenticated ? IS_NO_ACCESS : IS_NOT_AUTHORIZED;
  }

  const openStartOverPopup = () => {
    props.popupActions.openPopup({
      popupId: POPUP_ID_START_OVER,
      popupAriaLabelKey: '[aria label start over popup]',
      component: StartOverPopup,
      startNewAttempt: props.courseActions.startNewAttempt,
      navigateToIndex: props.navigateToIndex,
      disablePopupClosing: true,
      disableBottomLine: true
    });
  };

  const openTimedOutPopup = () => {
    props.popupActions.openPopup({
      popupId: POPUP_ID_TIMED_OUT,
      popupAriaLabelKey: '[aria label timed out popup]',
      component: TimedOut,
      disablePopupClosing: true
    });
  };

  useEffect(() => {
    if (isUserAuthenticated) {
      const navigate = async () => {
        const urlToNavigate: any = await props.courseActions.launch();
        props.navigateToUrl(urlToNavigate);
      };

      navigate();
    }
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (isCourseProgressRestoreFailed) {
      openStartOverPopup();
    }

    if (timerStatus === TimerStatus.TIMED_OUT && !isCourseProgressRestoreFailed) {
      openTimedOutPopup();
    }
    // eslint-disable-next-line
  }, [isCourseProgressRestoreFailed, timerStatus]);

  return (
    <Route
      render={({ location }) => (
        <ThemeProvider theme={{ colors, fonts }}>
          <RouteTransition
            location={location}
            isLowResolution={isLowResolution}
            isTocExpanded={isTocExpanded}
            background={background}
          >
            <Layout className="layout">
              <ErrorBoundary popup={openStartOverPopup}>
                <Switch location={location}>
                  <PrivateRoute
                    exact
                    path={INDEX_PATH}
                    component={Sections}
                    componentNoAccess={NoAccess}
                    typeAccess={typeAccess}
                    materialType={MATERIAL_TYPES.COURSE}
                  />
                  <PrivateRoute
                    exact
                    path={SECTIONS_PATH}
                    component={Sections}
                    componentNoAccess={NoAccess}
                    typeAccess={typeAccess}
                    materialType={MATERIAL_TYPES.COURSE}
                  />
                  <PrivateRoute
                    exact
                    path={RESULTS_PATH}
                    component={CourseOverview}
                    componentNoAccess={NoAccess}
                    typeAccess={typeAccess}
                    materialType={MATERIAL_TYPES.COURSE}
                  />
                  <PrivateRoute
                    path={SECTION_PATH}
                    component={CourseOverview}
                    componentNoAccess={NoAccess}
                    typeAccess={typeAccess}
                    materialType={MATERIAL_TYPES.COURSE}
                  />
                  <RouteWithRedirect
                    exact
                    path={LOGIN_PATH}
                    shouldRedirect={isUserAuthenticated}
                    component={Login}
                  />
                  <RouteWithRedirect
                    exact
                    path={SIGNUP_PATH}
                    shouldRedirect={isUserAuthenticated}
                    component={SignUp}
                  />
                  <Route path="*" exact component={NotFoundPage} />
                </Switch>
              </ErrorBoundary>
            </Layout>
          </RouteTransition>
        </ThemeProvider>
      )}
    />
  );
};

function mapStateToProps(state: RootAppState) {
  return {
    isCourseProgressRestoreFailed: isProgressRestoreFailed(state),
    timerStatus: getTimerStatus(state)
  };
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    courseActions: bindActionCreators(courseActions, dispatch),
    popupActions: bindActionCreators(popupActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Course);
